<template>
  <PageBody style="padding:0">
    <div class="list-container">
      <ul class="list-type">
        <router-link tag="li" to="/show/public/all" active-class="primary-color">所有人</router-link>
        <router-link tag="li" to="/show/public/male" active-class="primary-color">男会员</router-link>
        <router-link tag="li" to="/show/public/female" active-class="primary-color">女会员</router-link>
      </ul>
      <div class="query-body">
        <Input size="default" prefix="ios-search" placeholder="昵称／个签关键字搜索" style="width: 200px" :clearable="true"
               :value="$route.query.keyword" @on-change="handleSearch"/>
        <router-link tag="button" class="add-btn gym-btn plain-btn"
                     :to="{ path: '/show/detail',query:{sex}}">添加会员秀
        </router-link>
      </div>
      <div class="list-body" v-loading="isLoading">
        <p v-if="!listData.length" class="empty-list">
          <span v-if="$route.query.keyword">没有匹配信息</span>
          <span v-else>暂无数据</span>
        </p>
        <ul v-else class="list-ul">
          <li class="picture-box" v-for="(picture,i) in listData" :key="i">
            <img :src="picture.image" class="picture-img" @click="handleZoom(picture)">
            <div class="picture-buttons">
              <Tooltip content="编辑">
                <router-link tag="button" :to="'/show/detail/'+picture.id">
                  <i class="iconfont gym-icon-edit"></i>
                </router-link>
              </Tooltip>
              <Tooltip content="放大">
                <button @click="handleZoom(picture)">
                  <i class="iconfont gym-icon-zoom"></i>
                </button>
              </Tooltip>
              <Tooltip content="删除">
                <button @click="handleDelete(picture)">
                  <i class="iconfont gym-icon-trash"></i>
                </button>
              </Tooltip>
            </div>
            <div v-if="picture.signature" class="picture-describe text-ellipsis">{{picture.signature}}</div>
          </li>
        </ul>
      </div>
      <div class="paging-body">
        <Page
          :total="this.page.total"
          :current="this.page.pageNum"
          :page-size="this.page.pageSize"
          show-elevator show-total show-sizer :transfer="false"
          @on-change="pageNumChange"
          @on-page-size-change="pageSizeChange"
        />
      </div>
      <PictureDetail :visible="picture" @close="picture = false"/>
    </div>
  </PageBody>
</template>
<script>
import PictureList from 'views/components/PictureList'
import PictureDetail from './components/PictureDetail'
import { getData, deleteDataById } from 'api/show'
import list from '@/mixins/list'
import debounce from 'lodash/debounce'

export default {
  components: {PictureList, PictureDetail},
  mixins: [list],
  data () {
    return {
      query: {},
      isLoading: false,
      listData: [],
      picture: false,
      page: {
        pageNum: 1,
        pageSize: 10,
        total: 0
      }
    }
  },
  computed: {
    sex: function () {
      switch (this.$route.params.type) {
        case 'all':
          return 1
        case 'male':
          return 2
        case 'female':
          return 3
      }
    }
  },
  mounted () {

  },
  methods: {
    getList (data = {}) {
      this.isLoading = true
      let query = {
        pageSize: 10,
        pageNum: 1,
        sex: this.sex,
        keyword: ''
      }
      this.query = Object.assign({}, query, data)
      getData(this.query).then(res => {
        if (Number(res.code) === 200) {
          this.listData = res.data
          res.pageNum && (this.page.pageNum = Number(res.pageNum))
          res.pageSize && (this.page.pageSize = Number(res.pageSize))
          res.total && (this.page.total = Number(res.total))
        } else {
          this.listData = []
          this.$message({
            message: res.message,
            type: 'warning'
          })
        }
      }).finally(() => {
        this.isLoading = false
      })
    },
    handleSearch: debounce(function (e) {
      this.listQuery({
        keyword: e.target.value,
        pageNum: 1
      })
    }, 1000),
    pageNumChange (pageNum) {
      this.listQuery({
        pageNum: String(pageNum)
      })
    },
    pageSizeChange (pageSize) {
      this.listQuery({
        pageSize: String(pageSize)
      })
    },
    handleDelete (picture) {
      this.$confirm({
        title: '会员秀',
        brief: '确定删除此会员秀吗？',
        confirm: (dialog) => {
          dialog.loading = true
          deleteDataById({id: picture.id}).then(res => {
            if (Number(res.code) === 200) {
              dialog.destroy()
              this.listRefresh()
            } else {
              dialog.loading = false
              this.$message({
                message: res.message,
                type: 'warning'
              })
            }
          })
        }
      })
    },
    handleZoom (picture) {
      this.picture = picture
    }
  }
}
</script>
<style lang='stylus' scoped>
  .list-container
    width 100%
    height 100%
    display flex
    flex-direction column
    align-items stretch
  .query-body
    padding 0 20px
    height 72px
    display flex
    flex-direction row
    align-items center
    justify-content space-between
  .paging-body
    border-top 1px solid #EAEAEA
    height 60px
    width 100%
    padding-right 20px
    display: flex
    justify-content flex-end
    align-items center
  .list-body
    flex 1
    padding 0 20px
    overflow auto
  .list-ul
    display flex
    flex-wrap wrap
    margin-right -20px
    overflow hidden
  .picture-box
    margin-right 30px
    margin-bottom 20px
    width 130px
    height 156px
    background #F4F4F4
    display flex
    position relative
  .picture-img
    flex 1
  .picture-buttons
    position absolute
    top 6px
    left 0
    padding 0 6px
    width 100%
    display flex
    flex-direction row
    justify-content space-between
    button
      background rgba(237, 237, 237, .4)
      color #524D4D
      width 20px
      height 20px
      border-radius 4px
    .iconfont
      font-size 18px
      position relative
      top -4px
  .picture-describe
    padding 0 4px
    position absolute
    bottom 0
    width 100%
    height 24px
    line-height 24px
    background rgba(237, 237, 237, .4)
    color #2B3242
  .picture-buttons
  .picture-describe
    opacity 0
    transition opacity 0.2s
  .picture-box:hover .picture-buttons
  .picture-box:hover .picture-describe
    opacity 1
  .add-btn
    font-size 12px
    width 108px
  .empty-list
    width 100%
    height 100%
    display flex
    align-items center
    justify-content center
  .list-type
    height 52px
    display flex
    align-items stretch
    border-bottom 1px solid #D8D8D8
    & > li
      width 96px
      cursor pointer
      display flex
      justify-content center
      align-items center
</style>



